import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import home from './page/home.vue'
import pic from './page/pic.vue'
import article from './page/article.vue'
import film from './page/film.vue'
import tech from './page/tech.vue'
import user from './page/user.vue'
import addflow from './components/addflow.vue'
import flowdetail from './page/flowdetail.vue'
// import VueQuillEditor from 'vue-quill-editor'
// import ElementUI from 'element-ui'
import axios from 'axios'

Vue.config.productionTip = false

Vue.use(VueRouter)
Vue.use(axios)
// Vue.use(VueQuillEditor)
// Vue.use(ElementUI)

// import 'quill/dist/quill.core.css'
// import 'quill/dist/quill.snow.css'
// import 'element-ui/lib/theme-chalk/index.css'

const routes = [
  {
    path:"/",
    name:"home",
    component:home
  },
  {
    path:"/page/home",
    name:"home",
    component: home
  },
  {
    path:"/page/pic",
    name:"pic",
    component: pic
  },
  {
    path:"/page/article",
    name:"article",
    component: article
  },
  {
    path:"/page/film",
    name:"film",
    component: film
  },
  {
    path:"/page/tech",
    component: tech
  },
  {
    path:"/page/user",
    component: user
  },
  {
    path:"/components/addflow",
    component: addflow
  },
  {
    path:"/page/flowdetail",
    component: flowdetail
  }
]
const router = new VueRouter({
  mode: 'history',
  routes
});

new Vue({
  render: h => h(App),
  router
}).$mount('#app')

